import React from 'react';
import {Button, FormLayout, Panel, PanelHeader, Textarea, FormStatus, Alert} from '@vkontakte/vkui';

interface SendProps {
    id: string;
}

interface SendState {

}

class SendComponent extends React.Component<SendProps, SendState> {
    closePopout() {
        this.setState({ popout: null });
    }

    render() {
        return (
            <Panel id="send" className="header">
                <PanelHeader></PanelHeader>
                <img
                    className="Persik"
                    src={'./img/9.jpg'}
                />
                <FormLayout>
                <FormStatus title="  " state="error">
                          
                </FormStatus>
                    <Textarea
                        top="  , ,
                           ,      ."
                        placeholder=",  ....."
                    />
                    <Button size="xl" level="secondary" ></Button>
                </FormLayout>
            </Panel>

        );

    }
}

export const Send = (SendComponent);



